import React, { useState, useEffect } from 'react';
import countries from '../components/Countries';
import { parsePhoneNumber } from 'awesome-phonenumber';
import Logo from '../components/Logo';

const LoginForm = () => {
  const [countryCode, setCountryCode] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [phoneNumberError, setPhoneNumberError] = useState('');
  const [country, setCountry] = useState(null);

  useEffect(() => {
    // Fetch user's IP location
    fetch('https://ipapi.co/json/')
      .then((response) => response.json())
      .then((data) => {
        // Extract the country code from the API response
        const countryCode = data.country_code;

        // Set the country code if it exists
        if (countryCode && countries[countryCode]) {
          setCountryCode(countryCode);
        }
      })
      .catch((error) => {
        console.log('IP location error:', error);
      });
  }, []);

  useEffect(() => {
    // Update the country when countryCode changes
    if (countryCode && countries[countryCode]) {
      setCountry(countries[countryCode]);
    }
  }, [countryCode]);

  const handleCountryCodeChange = (e) => {
    setCountryCode(e.target.value);
  };

  const handlePhoneNumberChange = (e) => {
    setPhoneNumber(e.target.value);
    setPhoneNumberError('');
  };

  const handleSubmit = async(e) => {
    e.preventDefault();


    // Validate phone number
    if (!/^\d+$/.test(phoneNumber)) {
      setPhoneNumberError('Phone number should only contain digits (0-9)');
      return;
    }

    const pn = parsePhoneNumber(phoneNumber, { regionCode: countryCode });

    if (!pn.valid) {
      setPhoneNumberError('Invalid phone number');
      return;
    }

    // Perform login logic here
    // console.log('Country:', country);
    console.log('Phone Number:', phoneNumber);

  };

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <Logo />
        <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
          Login to your account
        </h2>
      </div>

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label htmlFor="countryCode" className="block text-sm font-medium text-gray-700">
              Select Country Code
              </label>
              <div className="mt-1 flex items-center">
                <select
                  id="countryCode"
                  name="countryCode"
                  value={countryCode}
                  onChange={handleCountryCodeChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  required
                >
                  <option value="">Select Country Code</option>
                  {Object.keys(countries).map((countryCode) => (
                    <option key={countryCode} value={countryCode}>
                      {`${countries[countryCode].flag} ${countries[countryCode].name} (${countries[countryCode].phone[0]})`}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <div>
              <label htmlFor="phoneNumber" className="block text-sm font-medium text-gray-700">
                Phone Number
              </label>
              <div className="mt-1">
                <input
                  id="phoneNumber"
                  name="phoneNumber"
                  type="tel"
                  autoComplete="tel"
                  required
                  value={phoneNumber}
                  onChange={handlePhoneNumberChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
              {phoneNumberError && (
                <p className="mt-2 text-sm text-red-600">{phoneNumberError}</p>
              )}
            </div>

            <div>
              <button
                type="submit"
                className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              >
                Send OTP
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default LoginForm;
